	module.exports = {"data":{"markdownRemark":{"html":"<p>This handsome Victorian Country House had many beautiful original features and the owners had some fine antique furniture and artwork but the rooms were dull and cluttered.</p>\n<p>The décor was dated and did not show the house off to its full potential.</p>\n<p>The brief was to completely redecorate the main reception rooms and three bedrooms including carpets, window treatments, lighting and some furniture.</p>\n<p>We fitted neutral wool textured carpet throughout which gave the whole area a lift and connected the rooms.</p>\n<p>The hall stairs and landing were given warm neutral stripe wallpaper above the dado and a darker neutral colour below together with new brass glass lanterns giving the entrance a bright welcoming feel.</p>\n<p>We added sumptuous rich silk stripe curtains in the dining room with a masculine feel to match the hunting theme in the artwork and rich mahogany furniture.</p>\n<p>The previously dark cluttered sitting room was transformed into a light comfortable summer room with soft shades of green in the silk curtains, pale textured chenille sofas and clean modern lamps. The furniture layout was completely reorganised focusing around the new log burning stove.</p>\n<p>The three bedrooms were completely redecorated with pale neutrals, roman blinds providing privacy with flowing linen voiles softening the windows.</p>","frontmatter":{"date":"January 01, 2012","slug":"2012-victorian-country-house","title":"Victorian Country House in Rode, Somerset","cover":"rode12.jpg"}},"galleriesJson":{"images":[{"title":"Dining room","path":"rode03.jpg"},{"title":"Sitting room corner view","path":"rode08.jpg"},{"title":"Sitting room lamps","path":"rode09.jpg"},{"title":"Sitting room sofas","path":"rode08.jpg"},{"title":"Sitting room with new sofas and log burning stove","path":"rode11.jpg"},{"title":"Sitting room detail","path":"rode13.jpg"},{"title":"Guest bedroom 1","path":"rode04.jpg"},{"title":"Details of headboard and cushions in guest bedroom 1","path":"rode02.jpg"},{"title":"Guest bedroom 2","path":"rode06.jpg"},{"title":"Guest bedroom 2 feature wall","path":"rode07.jpg"},{"title":"Guest bedroom 3","path":"rode01.jpg"}]}},"pathContext":{"slug":"2012-victorian-country-house"}}

